import React, {useState} from 'react'
import { Card, Table, Button, message, Menu, Modal, Avatar, Row, Col, Select, Form, InputNumber, Input, Tag, DatePicker } from 'antd';
import { EditOutlined, DeleteOutlined, PlusCircleOutlined, CarOutlined, ShareAltOutlined, EyeOutlined } from '@ant-design/icons';
import AvatarStatus from 'components/shared-components/AvatarStatus';
import EllipsisDropdown from 'components/shared-components/EllipsisDropdown';
import NumberFormat from 'react-number-format';
import Flex from 'components/shared-components/Flex'
import { useHistory } from "react-router-dom";
import { Typography } from "antd";
import utils from 'utils';
import { DataService } from "services/orders.service";
import { Balance } from 'functions/balance';
import { DataService as TripsService } from "services/trip.service";
import { DataService as OutsourceService } from "services/outsourcing.service";
import Invoice from '../invoice';
import moment from 'moment';

const { Title } = Typography;
const dateFormat = 'DD/MM/YYYY';
const { confirm } = Modal;
const { Option } = Select;
const getStatus = status => {
	if(status == 'Pending') {
		return <Tag className ="text-capitalize" color="#108ee9">Pending</Tag>
	}
	else if(status == 'En Route') {
		return <Tag className ="text-capitalize" color="#ffc542">En Route</Tag>
	}
	else if(status == 'Completed') {
		return <Tag className ="text-capitalize" color="#87d068">Completed</Tag>
	}
	else{
		return <Tag className ="text-capitalize" color='red'>Cancelled</Tag>
	}
}


const OrdersList = ({systemData}) => {
	let history = useHistory();

	const [allTripsLoading, setAllTripsLoading] = useState(true);
	const [allTrips, setAllTrips] = useState({}); 
	const [orderKey, setOrderkey] = useState('');
	const [form] = Form.useForm();
	const [order, setOrder] = useState({});
	const [newTrip, setNewTrip] = useState(false);
	const [outsource, setOutsource] = useState(false);
	const [confirmLoading, setConfirmLoading] = useState(false);
	const { orders, ordersLoading, trips, tripsLoading, avatarColors, allDestinationsLoading, allDestinations, allCurrencyLoading, allCurrency, trucks, trucksLoading, allRentedTrucks, garages, rentedTrucks, rentedTrucksLoading, currencies, currenciesLoading, outsourcing, outsourcingLoading, allTrucks, roadTall, allBorders, fuels, allCountries, allCountriesLoading, destinations, destinationsLoading, allTripsLoading:tripDataLoading, allTrips:tripData, rounder } = systemData;
	const [quantity, setQuantity] = useState(0);
	const [rate, setRate] = useState(0);
	const [price, setPrice] = useState(0);
	const [totalAmount, setTotalAmount] = useState(0);
	const [visible, setVisible] = useState(false);
	const [dates, setDates] = useState(new Date());
	const [roadTalls, setRoadTalls] = useState([]);
	const [orderTrips, setOrderTrips] = useState([]);
	const [orderGarage, setOrderGarage] = useState([]);
	const [orderOutsourced, setOrderOutsourced] = useState([]);
	const [orderFuel, setOrderFuel] = useState([]);
	const [externalTrips, setExternalTrips] = useState([]);
	const showDetails = (order) => {
		setOrder(order);
		let trps = trips.docs.filter(x=> x.data().orderId == order._id);
		let outs = outsourcing.docs.filter(x=> x.data().orderId == order._id);
		let talls = roadTall.docs.filter(x=> x.data().orderId == order._id);
		let fls = fuels.docs.filter(x=> x.data().orderId == order._id);
		let grg = garages.docs.filter(x=> x.data().orderId == order._id);
		
		setOrderTrips(trps);
		setOrderOutsourced(outs);
		setRoadTalls(talls);
		setOrderFuel(fls);
		setOrderGarage(grg);
		setVisible(true);
	};

	if(!tripsLoading && !ordersLoading && !outsourcingLoading){
		let allRecords = [];
		
		orders.docs.map((doc)=>{
			allRecords = {
				...allRecords,
				[doc.data()._id] : 0,
			}
		})

		outsourcing.docs.map((doc, index) => {
			if(allRecords[doc.data().orderId] !== undefined)
			{
				allRecords[doc.data().orderId] = doc.data().status == 'Delivered' ? allRecords[doc.data().orderId] + doc.data().quantity : 	allRecords[doc.data().orderId]
			}
			else{
				allRecords[doc.data().orderId]=0;
			}
		})
		let extTrips = {};
		trips.docs.map((doc, index) => {

			if(!extTrips[doc.data().truckId]) extTrips[doc.data().truckId]=1;
			else extTrips[doc.data().truckId] = extTrips[doc.data().truckId] + 1;
			

			if(allRecords[doc.data().orderId] !== undefined)
			{
				allRecords[doc.data().orderId] = doc.data().status == 'Delivered' ? allRecords[doc.data().orderId] + doc.data().quantity : 	allRecords[doc.data().orderId]
			}
			else{
				allRecords[doc.data().orderId]=0;
			}
			if(index+1 == trips.docs.length && allTripsLoading){
				setAllTripsLoading(false);
				setAllTrips(allRecords);
				setExternalTrips(extTrips);
			}
		})
	}

	const dropdownMenu = row => (
		<Menu>
			<Menu.Item onClick={() => {showDetails(row)}}>
				<Flex alignItems="center">
					<EyeOutlined />
					<span className="ml-2">View Details</span>
				</Flex>
			</Menu.Item>
			<Menu.Item onClick={() => {addTrip(row)}}>
				<Flex alignItems="center">
					<CarOutlined />
					<span className="ml-2">Add trip</span>
				</Flex>
			</Menu.Item>
			<Menu.Item onClick={() => {outsourceTruck(row)}}>
				<Flex alignItems="center">
					<ShareAltOutlined />
					<span className="ml-2">Trip for rented truck</span>
				</Flex>
			</Menu.Item>
			<Menu.Item onClick={() => {viewDetails(row)}}>
				<Flex alignItems="center">
					<EditOutlined />
					<span className="ml-2">Edit Details</span>
				</Flex>
			</Menu.Item>
			<Menu.Item onClick={() => showConfirm(row)}>
				<Flex alignItems="center">
					<DeleteOutlined />
					<span className="ml-2">Delete</span>
				</Flex>
			</Menu.Item>
		</Menu>
	);

	const handleDateChange = date => {
    
		setDates(date ? new Date(date.format('YYYY-MM-DD HH:mm:ss')) : new Date());
	};

  	const disabledDate = (current) => {
		return current && current > moment().endOf("day");
	} 


	const completedMenu = row => (
		<Menu>
			<Menu.Item onClick={() => {showDetails(row)}}>
				<Flex alignItems="center">
					<EyeOutlined />
					<span className="ml-2">View Details</span>
				</Flex>
			</Menu.Item>
		</Menu>
	);
	
	const addTrip = (row) => {
		setOrder(row);
		setOrderkey(row._id);
		setNewTrip(true);
	}

	const outsourceTruck = (row) => {
		setOrder(row);
		setOrderkey(row._id);
		setOutsource(true);
	}

	const cancelTrip = () => {
		setNewTrip(false);
		setOutsource(false);
		setOrder({});
		setOrderkey();
	}


	const handleQuantityChange = (e) => {
		let value = e.target.value;
		let total = Math.round(rate*price*value);
		setQuantity(value);
		setTotalAmount(total);
	  }
	
	  const handlePriceChange = (value) => {
		let total = Math.round(rate*quantity*value);
		setPrice(value);
		setTotalAmount(total);
	  }

	const handleRateChange = (value) => {
		let total = Math.round(value*price*quantity);
		setRate(value);
		setTotalAmount(total);
	}

	const handleNewTrip = () => {
		setConfirmLoading(true);
		form.validateFields().then(values => {
			if(values.quantity == 0) {
				setConfirmLoading(false);
				return message.error('Please enter quantity to be delivered');
			}
			else if(values.quantity > +order.quantity) {
				setConfirmLoading(false);
				return message.error('Quantity is higher than ordered quantity');
			}
			setTimeout(() => {
				let userData = {
					exchangeRate:order.rate,
					currency:order.currency, 
					totalAmount:order.rate*order.price,
					tonPrice:order.price,
					milleage : values.milleage,
					truckId: values.truckId,
					quantity: values.quantity,
					departure: order.departure,
					destination: values.destination,
					orderId: orderKey,
					internal:true,
					status: 'En Route'
				}
				TripsService.create(userData,+(dates.getTime()));
				DataService.update(orderKey,{'status':'En Route'});
				setConfirmLoading(false);
				cancelTrip();
				history.push(`/app/apps/trips`)
				message.success(`Trip have been scheduled`);
				
			}, 1500);
		}).catch(info => {
			setConfirmLoading(false)
			console.log(info)
			message.error('Please enter all required field ');
		});
	}

	const handleNewOutsource = () => {
		setConfirmLoading(true);
		form.validateFields().then(values => {
			let currency = values.currency;
			let exchangeRate = values.exchangeRate;
			let tonPrice = values.tonPrice;
			if(values.quantity == 0) {
				setConfirmLoading(false);
				return message.error('Please enter quantity to be delivered');
			}
			else if(values.quantity > +order.quantity) {
				setConfirmLoading(false);
				return message.error('Quantity is higher than ordered quantity');
			}
			setTimeout(() => {
				let userData = {
					exchangeRate,
					currency, 
					totalAmount,
					tonPrice,
					milleage : 0,
					truckId: values.truckId,
					quantity: values.quantity,
					departure: order.departure,
					destination: values.destination,
					orderId: orderKey,
					internal:false,
					status: 'En Route'
				}
				TripsService.create(userData,+(dates.getTime()));
				DataService.update(orderKey,{'status':'En Route'});
				setConfirmLoading(false);
				cancelTrip();
				history.push(`/app/apps/trips`)
				message.success(`Truck have been outsourced`);
				
			}, 1500);
		}).catch(info => {
			setConfirmLoading(false)
			message.error('Please enter all required field ');
		});
	}

	const viewDetails = row => {
		history.push(`/app/apps/orders/edit/${row._id}`)
	}
	
	const newOrder = row => {
		history.push(`/app/apps/orders/add`)
	}

	const deleteRow = row => {
		row.status = false;
		DataService.update(row._id,row);
		message.success({ content: `Order have been deleted `, duration: 3 });
	}

	function showConfirm(row) {
		confirm({
		  title: `Do you want to delete this order of ${row.client} for ${row.quantity} tons of ${row.item}?`,
		  onOk() {
			deleteRow(row)
		  },
		  onCancel() {},
		});
	}

	const tableColumns = [
		{
			title: 'Date',
			dataIndex: 'date',
			render: (_, record) => Balance.getNormalDate(record.data()._id)
		},
		{
			title: 'Client',
			dataIndex: 'client',
			render: (_,record) => (
				<div className="d-flex" style={{cursor:'pointer'}}>
					<AvatarStatus size={0} type="square" src={null} name={record.data().client} subTitle={record.data().clientNumber}/>
				</div>
			  )
		},
		{
			title: 'Item to ship',
			dataIndex: 'quantity',
			render: (_, record) => (record.data().item)
		},
		{
			title: 'Tonnage',
			dataIndex: 'quantity',
			render: (_, record) => (
						<NumberFormat displayType={'text'} 
							value={record.data().quantity}
							thousandSeparator={true}/>
					)
		},
		{
			title: 'Price/Ton',
			dataIndex: 'price',
			render: (_, record) => (
						<NumberFormat displayType={'text'} 
							value={record.data().price}
							suffix={` ${allCurrencyLoading ? '' : allCurrency[record.data().currency]?.name}`} 
							thousandSeparator={true}/>
					)
		},
		{
			title: 'Total',
			dataIndex: 'price',
			render: (_, record) => (
						<NumberFormat displayType={'text'} 
							value={record.data().price*record.data().rate*record.data().quantity}
							suffix={` RWF`} 
							thousandSeparator={true}/>
					)
		},
		{
			title: 'Delivered',
			dataIndex: 'quantity',
			render: (_, record) => (
						<NumberFormat displayType={'text'} 
							value={allTrips[record.data()._id]?allTrips[record.data()._id]:0}
							thousandSeparator={true}/>
					)
		},
		
		{
			title: 'Location',
			dataIndex: 'status',
			render: (_,record) => (
				<div className="d-flex" style={{cursor:'pointer'}}>
					<AvatarStatus size={0} type="square" src={null} name={allDestinationsLoading ? '' : allDestinations[record.data().departure]?.name +' - '+allDestinations[record.data().departure]?.country} subTitle={allDestinationsLoading ? '' : 'TO: '+allDestinations[record.data().destination]?.name +' - '+allDestinations[record.data().destination]?.country}/>
				</div>
			  )
		},
		{
			title: 'Status',
			dataIndex: 'status',
			render: (_, record) => (
				<Flex alignItems="center">{getStatus(!allTrips[record.data()._id] ? 'Pending' : (allTrips[record.data()._id] == record.data().quantity ? 'Completed' : 'En Route'))}</Flex>
			),
		},
		{
			title: '',
			dataIndex: 'actions',
			render: (_, elm) => (
				<div className="text-right">
					{allTrips[elm.data()._id] == elm.data().quantity 
					?  
					<EllipsisDropdown menu={completedMenu(elm.data())}/>
					:
					<EllipsisDropdown menu={dropdownMenu(elm.data())}/>
					}
				</div>
			)
		}
	];

	return (
		<Form
			layout="vertical"
			form={form}
			name="advanced_search"
			className="ant-advanced-search-form"
		>
			<Card className={(visible) ? 'd-none' : ''}>
				<Flex alignItems="center" justifyContent="between" mobileFlex={false}>
					<Flex className="mb-1" mobileFlex={false}>
						<div className="mr-md-3 mb-3">
							<Title level={4}>Orders List</Title>
						</div>
					</Flex>
					<div>
						<Button size="small" onClick={newOrder} type="primary" icon={<PlusCircleOutlined />} block>New order</Button>
					</div>
				</Flex>
				<div className="table-responsive">
					<Table 
						columns={tableColumns} 
						dataSource={!ordersLoading ? orders.docs.reverse().filter(x => x.data().status) : []} 
						loading={ordersLoading}
						rowKey='_id' 
						size='small'
					/>
				</div>
			</Card>
			<Modal
				title={`New trip for order of ${order?.client} for ${order?.quantity} tons of ${order?.item}`}
				visible={newTrip}
				onOk={handleNewTrip}
				confirmLoading={confirmLoading}
				onCancel={cancelTrip}
			>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={24}>
						<Form.Item
							label="Truck to go on trip"
							name="truckId"
							rules={[{ required: true,message: `Truck is required` }]}
							style={{ display: 'block'}}
							className="w-100" 
						>
							<Select
							className="w-100"
							placeholder="-Select-"
							showSearch
							optionFilterProp="children"
							>
								{trucksLoading ? null : trucks.docs.map((truck)=><Option value={truck.data()._id}>{truck.data().plateNumber} - Driver: {truck.data().driverName}</Option>)}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={12} md={12}>
						<Form.Item name="dates" label="Date of trip" rules={[
							{
							required: true,
							message: `When truck went on trip?`,
							},
						]}>
							<DatePicker className="w-100" defaultValue={moment(dates, dateFormat)} format={dateFormat} disabledDate={disabledDate} onChange={handleDateChange}/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={12} md={12}>
						<Form.Item 
							name="destination" 
							label="Destination Address" 
							rules={[{ required: true,message: `Destination is required` }]}
							style={{ display: 'block'}}
							className="w-100" 
						>
							<Select
									className="w-100"
									showSearch
									placeholder="Select destination"
									optionFilterProp="children"
								>
								{!destinationsLoading && destinations.docs.map((address, index) => (
									<Option key={index} value={address.data()._id}>
										{address.data().name+' - '+address.data().country}
									</Option>
								))}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={12} md={12}>
						<Form.Item
							label="Milleage"
							name="milleage"
							rules={[{ required: newTrip, message: `Milleage is required` }]}
							style={{ display: 'block'}}
							className="w-100" 
						>
							<InputNumber
								className="w-100"
								min={0}
								parser={value => value.replace(/\$\s?|(,*)/g, '')}
                      			formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
							/>
						</Form.Item>
					</Col>
					
					<Col xs={24} sm={12} md={12}>
						<Form.Item
							label={`Tonnage`}
							name="quantity"
							onChange={handleQuantityChange}
							rules={[{ required: true, message: `Quantity is required` }]}
							style={{ display: 'block'}}
							className="w-100" 
						>
							<InputNumber
								className="w-100"
								min={0}
								max={order.quantity}
								parser={value => value.replace(/\$\s?|(,*)/g, '')}
                      			formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
							/>
						</Form.Item>
					</Col>
				</Row>
			</Modal> 
			<Modal
				title={`Trip for rented trucks on order of ${order?.client} of ${order?.item}`}
				visible={outsource}
				onOk={handleNewOutsource}
				confirmLoading={confirmLoading}
				onCancel={cancelTrip}
			>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={24}>
						<Form.Item
							label="Truck to go on trip"
							name="truckId"
							rules={[{ required: true,message: `Truck is required` }]}
							style={{ display: 'block'}}
							className="w-100" 
						>
							<Select
							className="w-100"
							placeholder="-Select-"
							showSearch
							optionFilterProp="children"
							>
								{/* {rentedTrucksLoading ? null : rentedTrucks.docs.filter(x => externalTrips[x.data()._id] ? (+x.data().trips > +externalTrips[x.data()._id]) : true).map((truck)=><Option value={truck.data()._id}>{truck.data().plateNumber} - Driver: {truck.data().driverName}</Option>)} */}
								{rentedTrucksLoading ? null : rentedTrucks.docs.map((truck)=><Option value={truck.data()._id}>{truck.data().plateNumber} - Driver: {truck.data().driverName}</Option>)}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={12} md={12}>
						<Form.Item name="dates" label="Date of trip" rules={[
							{
							required: true,
							message: `When truck went on trip?`,
							},
						]}>
							<DatePicker className="w-100" defaultValue={moment(dates, dateFormat)} format={dateFormat} disabledDate={disabledDate} onChange={handleDateChange}/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={12} md={12}>
						<Form.Item 
							name="destination" 
							label="Destination Address" 
							rules={[{ required: true,message: `Destination is required` }]}
							style={{ display: 'block'}}
							className="w-100" 
						>
							<Select
									className="w-100"
									showSearch
									placeholder="Select destination"
									optionFilterProp="children"
								>
								{!destinationsLoading && destinations.docs.map((address, index) => (
									<Option key={index} value={address.data()._id}>
										{address.data().name+' - '+address.data().country}
									</Option>
								))}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={12} md={12}>
						<Form.Item
							label={`Tonnage`}
							name="quantity"
							onChange={handleQuantityChange}
							rules={[{ required: true, message: `Quantity is required` }]}
							style={{ display: 'block'}}
							className="w-100" 
						>
							<InputNumber
								className="w-100"
								min={0}
								max={order.quantity}
								parser={value => value.replace(/\$\s?|(,*)/g, '')}
                      			formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
							/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={12} md={12}>
                  <Form.Item name="tonPrice" label="Price per ton" rules={[
                    {
                      required: true,
                      message: `Price per ton is required.`,
                    },
                  ]}>
                    <InputNumber
                      className="w-100"
                      min={0}
                      onChange={handlePriceChange}
                      formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, '')}
                      parser={value => value.replace(/\$\s?|(,*)/g, '')}
                    />
                  </Form.Item>
                </Col>
                <Col xs={24} sm={12} md={12}>
                  <Form.Item name="currency" label="Select currency used" rules={[
                    {
                      required: true,
                      message: `Currency is required.`,
                    },
                  ]}>
                    <Select
                      className="w-100 d-block"
                      placeholder="- Select Currency -"
                    >
                        {!currenciesLoading ? currencies.docs.map((currency)=><Option value={currency.data()._id} className='w-100 pe-5'>{currency.data().name}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</Option>) : null }
                    </Select>
                  </Form.Item>
                </Col>
                <Col xs={24} sm={12} md={12}>
                  <Form.Item name="exchangeRate" label="Exchange Rate vs 1RWF" rules={[
                    {
                      required: true,
                      message: `rate is required.`,
                    },
                  ]}>
                    <InputNumber
                      className="w-100"
                      min={0}
                      onChange={handleRateChange}
                      formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, '')}
                      parser={value => value.replace(/\$\s?|(,*)/g, '')}
                    />
                  </Form.Item>
                </Col>
                <Col xs={24} sm={24} md={24}>
                  <Title level={4}>
                    <NumberFormat
                      displayType={'text'} 
                      value={totalAmount}
                      suffix={'RWF'} 
                      prefix={'Total RWF: '} 
                      thousandSeparator={true} 
                    />
                  </Title>
                </Col>
				</Row>
			</Modal> 
			{visible &&(<Invoice 
				systemData={systemData} 
				roadTalls={roadTalls} 
				order={order} 
				orderTrips={orderTrips} 
				allDestinationsLoading={allDestinationsLoading} 
				allDestinations={allDestinations} 
				allCurrencyLoading={allCurrencyLoading} 
				allCurrency={allCurrency}
				allTrucks={allTrucks}
				allTrips={allTrips}
				orderOutsourced={orderOutsourced}
				allBorders={allBorders}
				orderFuel={orderFuel}
				orderGarage={orderGarage}
				allCountries={allCountries}
				allCountriesLoading={allCountriesLoading}
				allRentedTrucks={allRentedTrucks}
				tripDataLoading={tripDataLoading}
				tripData={tripData}
				rounder={rounder}
			/>)}
		</Form>
	)
}

export default OrdersList
