import React, { useState, useEffect } from 'react'
import PageHeaderAlt from 'components/layout-components/PageHeaderAlt'
import { Tabs, Form, Button, message } from 'antd';
import Flex from 'components/shared-components/Flex'
import GeneralField from './GeneralField'
import { useHistory } from "react-router-dom";
import { useCollection } from "react-firebase-hooks/firestore";
import { DataService } from "services/orders.service";
import { DataService as DestinationsService} from "services/destinations.service";
import { DataService as CurrenciesService } from "services/currencies.service";
import LocationData from "assets/data/district.json"
import moment from "moment";

const { TabPane } = Tabs;

const ADD = 'ADD'
const EDIT = 'EDIT'

const OrderForm = props => {

	const { mode = ADD, param, systemData } = props
	const [form] = Form.useForm();
	const [provinceData] = useState(LocationData);
	const [submitLoading, setSubmitLoading] = useState(false)
	const [destinations, destinationsLoading] = useCollection(DestinationsService.getAll());
	const [currencies, currenciesLoading] = useCollection(CurrenciesService.getAll());	

	const [rate, setRate] = useState(1);
	const [price, setPrice] = useState(0);
	const [totalAmount, setTotalAmount] = useState(0);
	const [quantity, setQuantity] = useState(0);

	const handlePriceChange = (value) => {
		let total = rate*quantity*value;
		setPrice(value);
		setTotalAmount(total);
	}

	const handleQuantityChange = (value) => {
		let total = rate*price*value;
		setQuantity(value);
		setTotalAmount(total);
	}

	const handleRateChange = (value) => {
		let total = value*price;
		setRate(value);
		setTotalAmount(total);
	}

	let history = useHistory();

	useEffect(() => {
    	if(mode === EDIT) {
			const { id } = param
			DataService.getRow(id).then(data =>{
				let user = data.data()
				form.setFieldsValue({
					client: user.client,
					clientNumber: user?.clientNumber,
					item: user.item,
					quantity: user.quantity,
					departure: user.departure,
					destination: user.destination,
					price: user?.price,
					currency: user?.currency,
					rate: user?.rate,
				});
				setTotalAmount(user.totalAmount);
			});
		}
  	}, [form, mode, param, props, provinceData]);


	const onDiscard = () => {
		setSubmitLoading(false)
		history.push(`/app/apps/orders`)
	}

	const onFinish = () => {		
		setSubmitLoading(true)
		form.validateFields().then(values => {
			setTimeout(() => {
				let clientData = {
					client : values.client,
					clientNumber : values.clientNumber,
					item: values.item,
					departure:values.departure,
					destination:values.destination,
					quantity: values.quantity,
					price: values.price,
					currency: values.currency,
					rate:values.rate
				}
				
				if(mode === ADD) {
					const { userData } = systemData;
					
					clientData.status = 'Pending'
					clientData.backupUser=userData.displayName
					clientData.userId=userData.userId;

					DataService.create(clientData)
					onDiscard();
					message.success(`New order have been added to orders list`);
				}
				if(mode === EDIT) {
					let { id } = param
					DataService.update(id,clientData);
					onDiscard();
					message.success(`order details updated`);
					
				}
			}, 1500);
		}).catch(info => {
			setSubmitLoading(false)
			console.log('info', info)
			message.error('Please enter all required field ');
		});
	};

	return (
		<>
			<Form
				layout="vertical"
				form={form}
				name="advanced_search"
				className="ant-advanced-search-form"
				initialValues={{
					heightUnit: 'cm',
					widthUnit: 'cm',
					weightUnit: 'kg'
				}}
			>
				<PageHeaderAlt className="border-bottom" overlap>
					<div className="container">
						<Flex className="py-2" mobileFlex={false} justifyContent="between" alignItems="center">
							<h2 className="mb-2">{mode === 'ADD'? 'Add New Order' : `Edit Order Details`} </h2>
							<div className="mb-3">
								<Button className="mr-2" onClick={() => onDiscard()}>Discard</Button>
								<Button type="primary" onClick={() => onFinish()} htmlType="submit" loading={submitLoading} >
									{mode === 'ADD'? 'Add' : `Save`}
								</Button>
							</div>
						</Flex>
					</div>
				</PageHeaderAlt>
				<div className="container">
					<Tabs defaultActiveKey="1" style={{marginTop: 30}}>
						<TabPane tab="Order form" key="1">
							<GeneralField 
								systemData={systemData}
								destinations={destinations}
								destinationsLoading={destinationsLoading}
								handlePriceChange={handlePriceChange}
								currencies={currencies}
								currenciesLoading={currenciesLoading}
								handleQuantityChange={handleQuantityChange}
								handleRateChange={handleRateChange}
							/>
						</TabPane>
					</Tabs>
				</div>
			</Form>
		</>
	)
}

export default OrderForm
