import React from 'react'
import { Route, Switch, Redirect } from 'react-router-dom';
import Add from './add-order'
import Edit from './edit-order'
import List from './orders-list'

const Milleage = ({match, systemData}) => {
	return (
		<Switch>
			<Redirect exact from={`${match}`} to={`${match}/list`} />
			<Route path={`${match}/add`} render={ () => (<Add systemData={systemData}/>) } />
			<Route path={`${match}/edit/:id`} component={Edit} />			
			<Route path={`${match}/list`} render={ () => (<List systemData={systemData}/>) } />
		</Switch>
	)
}

export default Milleage

