import React, {useState} from 'react'
import { Row, Col, Card, Form, InputNumber, Select, Input } from 'antd';

const { Option } = Select;

const rules = {
	departure: [
		{
			required: true,
			message: 'Select departure location',
		}
	],
	destination: [
		{
			required: true,
			message: 'Select destination location'
		}
	],
	item: [
		{
			required: true,
			message: 'Please enter item name',
		}
	],
	client: [
		{
			required: true,
			message: 'Client name is required',
		}
	],
	clientNumber: [
		{
			required: true,
			message: 'Client number is required',
		}
	],
	quantity: [
		{
			required: true,
			message: 'Enter tonnage to ship',
		},
	],
	price: [
		{
			required: true,
			message: 'Price per ton is required',
		},
	],
	currency: [
		{
			required: true,
			message: 'Price per ton is required',
		},
	],
	rate: [
		{
			required: true,
			message: 'Exchange rate is required',
		},
	],
}

const GeneralField = props => {
	const {destinations, destinationsLoading, currenciesLoading, currencies, handleQuantityChange, handlePriceChange, handleRateChange} = props;


	return (
		
	<Row gutter={16} className='justify-content-center'>
		
		<Col xs={24} sm={24} md={17}>
			<Card>
				<Row gutter={16}>
					<Col xs={24} sm={24} md={8}>
						<Form.Item name="client" label="Client name" rules={rules.client}>
							<Input className="w-100"/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={8}>
						<Form.Item name="clientNumber" label="Client number" rules={rules.clientNumber}>
							<Input className="w-100"/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={8}>
						<Form.Item name="item" label="Item to ship" rules={rules.item}>
							<Input className="w-100"/>
						</Form.Item>
					</Col>
					
					<Col xs={24} sm={12} md={6}>
						<Form.Item
							label={`Tonnage`}
							name="quantity"
							onChange={handleQuantityChange}
							rules={[{ required: true, message: `Quantity is required` }]}
							style={{ display: 'block'}}
							className="w-100" 
						>
							<InputNumber
								className="w-100"
								min={0}
								parser={value => value.replace(/\$\s?|(,*)/g, '')}
                      			formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
							/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={12} md={6}>
						<Form.Item name="price" label="Price per ton" rules={[
							{
							required: true,
							message: `Price per ton is required.`,
							},
						]}>
							<InputNumber
							className="w-100"
							min={0}
							onChange={handlePriceChange}
							formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
							parser={value => value.replace(/\$\s?|(,*)/g, '')}
							/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={12} md={6}>
						<Form.Item name="currency" label="Select currency used" rules={[
							{
							required: true,
							message: `Currency is required.`,
							},
						]}>
							<Select
							className="w-100 d-block"
							placeholder="- Select Currency -"
							>
								{!currenciesLoading ? currencies.docs.map((currency)=><Option value={currency.data()._id} className='w-100 pe-5'>{currency.data().name}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</Option>) : null }
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={12} md={6}>
						<Form.Item name="rate" label="Exchange rate" rules={[
							{
							required: true,
							message: `Exchange rate is required.`,
							},
						]}>
							<InputNumber
							className="w-100"
							min={0}
							onChange={handleRateChange}
							formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
							parser={value => value.replace(/\$\s?|(,*)/g, '')}
							/>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12}>
						<Form.Item name="departure" label="Departure Address" rules={rules.departure}>
							<Select
									className="w-100"
									showSearch
									placeholder="Select departure"
									optionFilterProp="children"
								>
								{!destinationsLoading && destinations.docs.map((address, index) => (
									<Option key={index} value={address.data()._id}>
										{address.data().name+' - '+address.data().country}
									</Option>
								))}
							</Select>
						</Form.Item>
					</Col>
					<Col xs={24} sm={24} md={12}>
						<Form.Item name="destination" label="Destination Address" rules={rules.destination}>
							<Select
									className="w-100"
									showSearch
									placeholder="Select destination"
									optionFilterProp="children"
								>
								{!destinationsLoading && destinations.docs.map((address, index) => (
									<Option key={index} value={address.data()._id}>
										{address.data().name+' - '+address.data().country}
									</Option>
								))}
							</Select>
						</Form.Item>
					</Col>
				</Row>
			</Card>
		</Col>
	</Row>
)}

export default GeneralField
