import React, {useState} from 'react'
import { PrinterOutlined, CloseOutlined, CheckOutlined, CloseCircleOutlined, ArrowLeftOutlined } from '@ant-design/icons';
import { Card, Table, Button, Tag, Alert } from 'antd';
import NumberFormat from 'react-number-format';
import { useHistory } from "react-router-dom";
import Flex from 'components/shared-components/Flex'
import { Balance } from 'functions/balance';
 
const { Column } = Table; 
const Invoice = (props) => { 	
	let history = useHistory();	
	const RedirectBack = () => {
		history.push(`/app/apps/orders`)
	}
	const getStatus = status => {
		if(status == 'Pending') {
			return <Tag className ="text-capitalize" color="#108ee9">Pending</Tag>
		}
		else if(status == 'En Route') {
			return <Tag className ="text-capitalize" color="#ffc542">En Route</Tag>
		}
		else if(status == 'Delivered') {
			return <Tag className ="text-capitalize" color="#87d068">Delivered</Tag>
		}
		else{
			return <Tag className ="text-capitalize" color='red'>Cancelled</Tag>
		}
	}
	
	const { systemData, order, orderTrips, allCurrencyLoading, allCurrency, allDestinationsLoading, allDestinations, allTrucks, allTrips, roadTalls, allBorders, orderFuel, orderGarage, allCountries, allCountriesLoading, allRentedTrucks, tripData, tripDataLoading, rounder } = props;
	const { userData } = systemData;

	let orderPrice = orderTrips.reduce((accumulator, currentValue) => accumulator + ((currentValue.data().totalAmount && currentValue.data().milleage) ? currentValue.data().totalAmount : 0), 0);
	let totalMilleage = orderTrips.reduce((accumulator, currentValue) => accumulator + currentValue.data().milleage, 0);
	let totalTransported = orderTrips.reduce((accumulator, currentValue) => accumulator + ((currentValue.data().totalAmount && !currentValue.data().milleage) ? currentValue.data().totalAmount : 0), 0);
	let totalRoadtall = roadTalls.reduce((accumulator, currentValue) => accumulator + (currentValue.data().exchangeRate*currentValue.data().amount), 0);
	let totalFuel = orderFuel.reduce((accumulator, currentValue) => accumulator + currentValue.data().totalAmount, 0);
	let totalGarage = orderGarage.reduce((accumulator, currentValue) => accumulator + currentValue.data().totalAmount, 0);

	let grandTotal = orderPrice-totalMilleage-totalRoadtall-totalFuel-totalGarage;

	return (
		<div className="container">
			<Card>
				<div className="d-md-flex justify-content-md-between">
					<div>
						<img src="/img/logo.png" alt=""/>
					</div>
					<div className="mt-3 text-right">
						<h2 className="mb-1 font-weight-semibold">Order Details</h2>
						<p>{new Date(order.date).toLocaleDateString()}</p>
						<address>
							<p>
								<span className="font-weight-semibold text-dark font-size-md">{order.client}</span><br />
								{/* <span>{order.customerNumber}</span><br /> */}
							</p>
						</address>
					</div>
				</div>
				<div className="mt-4">
					<Table dataSource={[order]} pagination={false} className="mb-5"  size='small'>
						<Column title="Client" dataIndex="client" key="client" render={(_,record) => record.client }/>

						<Column title="Item to ship" dataIndex="item" key="item" render={(_,record) => record.item }/>
						
						<Column 
							title="Tonnage" 
							render={(_,record) => (
								<NumberFormat 
									displayType={'text'} 
									value={record.quantity} 
									thousandSeparator={true}
									/>
								)} 
								key="quantity"
								/>

						<Column 
							title="Price/Ton" 
							render={(_,record) => (
								<NumberFormat 
									displayType={'text'} 
									value={record.price} 
									thousandSeparator={true}
									suffix={` ${allCurrencyLoading ? '' : allCurrency[record.currency]?.name}`} 
								/>
							)} 
							key="quantity"
						/>

						<Column 
							title="Total" 
							render={(_,record) => (
								<NumberFormat 
									displayType={'text'} 
									value={record.price*record.rate*record.quantity} 
									thousandSeparator={true}
									suffix={` RWF`} 
								/>
							)} 
							key="quantity"
						/>

						<Column 
							title="Rate" 
							render={(_,record) => (
								<NumberFormat 
									displayType={'text'} 
									value={record.rate} 
									thousandSeparator={true}
								/>
							)} 
							key="quantity"
						/>

						<Column 
							title="Delivered" 
							render={(_,record) => (
								<NumberFormat 
									displayType={'text'} 
									value={allTrips[record._id]?allTrips[record._id]:0} 
									suffix={' Ton'} 
									thousandSeparator={true}
								/>
							)} 
							key="delivered"
						/>
						<Column 
							title="Location" 
							render={(_,record) => (
								<Flex alignItems="center">{allDestinationsLoading ? '' : allDestinations[record.departure]?.name +' - '+allDestinations[record.departure]?.country}</Flex>
							)} 
							key="departure"
						/>
						{/* <Column 
							title="Destination" 
							render={(_,record) => (
								<Flex alignItems="center">{allDestinationsLoading ? '' : allDestinations[record.destination]?.name +' - '+allDestinations[record.destination]?.country}</Flex>
							)} 
							key="destination"
						/> */}
					</Table>
				</div>

				<div className="mt-3">
					<h2 className="mb-1 font-weight-semibold">Trips</h2>
					<Table dataSource={orderTrips} pagination={false} className="mb-5"  size='small'>
						<Column title="Date" dataIndex="date" key="date" render={(_,record) => Balance.getNormalDate(record.data()._id)}/>
						
						<Column title="Truck" 
							render={ (_,record) => record.data().internal ? 
							`${allTrucks[record.data().truckId].plateNumber} - ${allTrucks[record.data().truckId].driverName}`
							:
							`${allRentedTrucks[record.data().truckId].plateNumber} - ${allRentedTrucks[record.data().truckId].driverName}`}
							key="truckId"
						/>
						
						<Column 
							title="Milleage" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={record.data().milleage} 
									thousandSeparator={true} 
								/>
							)} 
							key="milleage"
						/>

						<Column title="Roadtall" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={tripDataLoading ? '' : tripData[record.data()._id]?.roadTallAmount} 
									thousandSeparator={true} 
								/>
							)} 
							key="roadtall"
						/>

						<Column title="Fuel" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={tripDataLoading ? '' : tripData[record.data()._id]?.fuelAmount} 
									thousandSeparator={true} 
								/>
							)} 
							key="fuel"
						/>

						<Column title="Services" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={tripDataLoading ? '' : tripData[record.data()._id]?.garageAmount} 
									thousandSeparator={true} 
								/>
							)} 
							key="services"
						/>

						<Column title="Revenue" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={record.data().totalAmount} 
									thousandSeparator={true} 
								/>
							)} 
							key="revenue"
						/>

						<Column title="Expenses" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={tripDataLoading ? '' : tripData[record.data()._id]?.expenses} 
									thousandSeparator={true} 
								/>
							)} 
							key="expenses"
						/>

						<Column title="Balance" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={tripDataLoading ? '' : rounder(tripData[record.data()._id]?.balance)} 
									thousandSeparator={true} 
								/>
							)} 
							key="balance"
						/>

						<Column 
							title="Destination" 
							render={(_,record) => (
								<Flex alignItems="center">{allDestinationsLoading ? '' : allDestinations[record.data().destination]?.name +' - '+allDestinations[record.data().destination]?.country}</Flex>
							)} 
							key="destination"
						/>

						<Column title="Status" 
							render={(_,record) => getStatus(record.data().status)} 
							key="status"
						/>	
					</Table>
				</div>

				<div className="mt-3">
					<h2 className="mb-1 font-weight-semibold">Road Talls</h2>
					<Table dataSource={roadTalls} pagination={false} className="mb-5"  size='small'>
						<Column title="Date" dataIndex="date" key="date" render={(_,record) => new Date(+record.data()._id).toLocaleDateString() }/>
						
						
						<Column title="Truck" 
							render={ (_,record) => record.data().internal ? 
							`${allTrucks[record.data().truckId].plateNumber} - ${allTrucks[record.data().truckId].driverName}`
							:
							`${allRentedTrucks[record.data().truckId].plateNumber} - ${allRentedTrucks[record.data().truckId].driverName}`}
							key="truckId"
						/>				

						<Column title="Border" 
							render={(_,record) => `${allBorders[record.data().border]?.name} - ${allBorders[record.data().border]?.country}`} 
							key="border"
						/>		

						<Column 
							title="Price" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={record.data().amount} 
									suffix={` ${allCurrencyLoading ? '' : allCurrency[record.data().currency]?.name}`} 
									thousandSeparator={true} 
								/>
							)} 
							key="price"
						/>
						<Column 
							title="Rate" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={record.data().exchangeRate} 
									thousandSeparator={true} 
								/>
							)} 
							key="rate"
						/>
						<Column 
							title="Total RWF" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={record.data().exchangeRate*record.data().amount} 
									suffix={' RWF'} 
									thousandSeparator={true} 
								/>
							)} 
							key="totalAmount"
						/>
						
					</Table>
				</div>

				<div className="mt-3">
					<h2 className="mb-1 font-weight-semibold">Services</h2>
					<Table dataSource={orderGarage} pagination={false} className="mb-5"  size='small'>
						<Column title="Date" dataIndex="date" key="date" render={(_,record) => new Date(+record.data()._id).toLocaleDateString() }/>
						
						
						<Column title="Truck" 
							render={ (_,record) => `${allTrucks[record.data().truckId].plateNumber} - ${allTrucks[record.data().truckId].driverName}`}
							key="truckId"
						/>		

						<Column title="Description" 
							render={(_,record) => record.data().description}
							key="description"
						/>

						<Column 
							title="Price" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={record.data().amount} 
									suffix={` ${allCurrencyLoading ? '' : allCurrency[record.data().currency]?.name}`} 
									thousandSeparator={true} 
								/>
							)} 
							key="price"
						/>
						<Column 
							title="Rate" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={record.data().exchangeRate} 
									thousandSeparator={true} 
								/>
							)} 
							key="rate"
						/>
						<Column 
							title="Total RWF" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={record.data().totalAmount} 
									suffix={' RWF'} 
									thousandSeparator={true} 
								/>
							)} 
							key="totalAmount"
						/>
						
					</Table>
				</div>
				
				<div className="mt-3">
					<h2 className="mb-1 font-weight-semibold">Fuel Consumption</h2>
					<Table dataSource={orderFuel} pagination={false} className="mb-5"  size='small'>
						<Column title="Date" dataIndex="date" key="date" render={(_,record) => new Date(+record.data()._id).toLocaleDateString() }/>
						
						
						<Column title="Truck" 
							render={ (_,record) => record.data().internal ? 
							`${allTrucks[record.data().truckId].plateNumber} - ${allTrucks[record.data().truckId].driverName}`
							:
							`${allRentedTrucks[record.data().truckId].plateNumber} - ${allRentedTrucks[record.data().truckId].driverName}`}
							key="truckId"
						/>			

						<Column 
							title="Liters" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={record.data().liters} 
							  		suffix={'L'} 
									thousandSeparator={true} 
								/>
							)} 
							key="liters"
						/>

						<Column 
							title="Price/L" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={record.data().literPrice} 
									suffix={` ${allCurrencyLoading ? '' : allCurrency[record.data().currency]?.name}`} 
									thousandSeparator={true} 
								/>
							)} 
							key="literPrice"
						/>
						<Column 
							title="Rate" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={record.data().exchangeRate} 
									thousandSeparator={true} 
								/>
							)} 
							key="rate"
						/>
						<Column 
							title="Total RWF" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={record.data().totalAmount} 
									suffix={' RWF'} 
									thousandSeparator={true} 
								/>
							)} 
							key="totalAmount"
						/>
						
						<Column title="Station" 
							render={(_,record) => record.data().station}
							key="station"
						/>

						<Column title="Country" 
							render={(_,record) => allCountriesLoading ? '' :  allCountries[record.data().country]?.name }
							key="country"
						/>

					</Table>
					<div className="d-flex justify-content-end mt-4">
						<div className="text-right ">
							<div className="border-bottom">
								<p className="mb-2">
									<span>Order Price: </span>
									<NumberFormat 
										displayType={'text'} 
										value={orderPrice} 
										suffix={'Rwf'} 
										thousandSeparator={true}
									/>
								</p>
								<p className="mb-2">
									<span>Milleages Used: </span>
									<NumberFormat 
										displayType={'text'} 
										value={totalMilleage} 
										suffix={'Rwf'} 
										thousandSeparator={true}
									/>
								</p>
								
								<p className="mb-2">
									<span>Roadtall Paid: </span>
									<NumberFormat 
										displayType={'text'} 
										value={totalRoadtall} 
										suffix={'Rwf'} 
										thousandSeparator={true}
									/>
								</p>
								
								<p className="mb-2">
									<span>Fuel Consumption: </span>
									<NumberFormat 
										displayType={'text'} 
										value={totalFuel} 
										suffix={'Rwf'} 
										thousandSeparator={true}
									/>
								</p>
								
								<p className="mb-2">
									<span>Garage service: </span>
									<NumberFormat 
										displayType={'text'} 
										value={totalGarage} 
										suffix={'Rwf'} 
										thousandSeparator={true}
									/>
								</p>
							</div>
							<h2 className="font-weight-semibold mt-3">
								<span className="mr-1">Balance: </span>
								<NumberFormat 
									displayType={'text'} 
									value={rounder(grandTotal)} 
									suffix={'Rwf'} 
									thousandSeparator={true}
								/>
							</h2>
							<h2 className="font-weight-semibold mt-3">
								<span className="mr-1">Rented trucks: </span>
								<NumberFormat 
									displayType={'text'} 
									value={rounder(totalTransported)} 
									suffix={'Rwf'} 
									thousandSeparator={true}
								/>
							</h2>
						</div>
					</div>
				</div>

				

				<hr className="d-print-none"/>
				<div className="text-right d-print-none">
					<Button type="secondary" className="mr-2" onClick={() => RedirectBack()}>
						<ArrowLeftOutlined /> Back
					</Button>
					<Button type="primary" className={`mr-2 ${userData?.role === 'Operator' ? 'd-none' : ''}`} onClick={() => window.print()}>
						<PrinterOutlined  type="printer" />
						<span className="ml-1">Print</span>
					</Button>
				</div>

			</Card>
		</div>
	);
}

export default Invoice
